.dropdown {
  width: 300px;
  user-select: none;
  margin: 100px auto;
  position: relative;
  font-size: 16px;
  text-decoration: none;
}

.dropdown .dropdown-btn{
  padding: 15px 20px;
  background: #D81E5B;
  box-shadow: 3px 3px 10px 6px rgba(0,0,0,0.06);
  font-weight: bold;
  color:#ffff;
  display: flex;
  align-items: center;
  cursor: pointer;
  justify-content: space-between;
}
.dropdown .dropdown-content {
position: absolute;
top: 110%;
left: 0;
padding: 10px;
max-height: 300px;
overflow-y: auto;
box-shadow: 3px 3px 10px 6px rgba(0,0,0,0.06);
font-weight: bold;
/* color:#333; */
width: 95%;
}
 
.dropdown .dropdown-content .dropdown-item{
padding: 10px;
cursor: pointer;
transition: all 0.2s;
}

.dropdown-item a{
  text-decoration: none;
  color: #D81E5B;
}
.dropdown .dropdown-content .dropdown-item:hover{
  background: #f4f4f4;
  
}

.dropdown-icon svg{
  transition: all 0.25s ease-in-out;
}
.dropdown-icon.rotate svg {
 transform:rotate(-180deg)
}
